import logo from './logo.svg';
import './App.css';
import {useEffect, useState} from 'react';
import {useInterval} from './useInterval';

import skate13 from './assets/13-99-Skate_niclas-moser-XO6at6w4vMc-unsplash_Final (1).png';
import plane13 from './assets/13-100- Iron Giant_shutterstock_49889352113-100-Iron Giant_Final (1).png';
import cadillac32 from './assets/32-11-Cadillac_Pexels_grayscale-photography-of-vintage-car-beside-pavement-712615_Neil kelly_final (1).png';
import back32 from './assets/Def-arrière.png';

const imgs = {
    skate13, plane13, cadillac32, back32
}


const App = () => {

    const [size, setSize] = useState('13');
    const [img, setImg] = useState(null);
    const [running, setRunning] = useState(false);

    useEffect(() => {
        console.log('mount')
        const query = new URLSearchParams(window.location.search);
        const size = query.get('size');
        setSize(size);
        setRunning(true);
        switch (size){
            case '13':
                setImg('skate13')
                break;
            case '32':
                setImg('cadillac32')
                break;
        }
    }, []);

    useInterval(() => {
        console.log('tick', size, img);
        switch (size){
            case '13':
                setImg(prevImg  => prevImg === 'skate13' ? 'plane13' : 'skate13')
                break;
            case '32':
                setImg(prevImg  => prevImg === 'cadillac32' ? 'back32' : 'cadillac32')
                break;
        }
    }, running ? 10000 : null);

    console.log(size)
  return (
    <div className="App">
        {img && <img src={imgs[img]}/>}
    </div>
  );
}

export default App;
